.container {
	display: flex;
	width: 100%;
	height: 100vh;
	flex-direction: column;
}

.tabMenuBox {
	display: flex;
	width: 100%;
	height: 100px;
	border-bottom: 1px black solid;
	justify-content: center;
}

.logoBox {
	display: flex;
	flex: 1;
	height: 100%;
	background-color: green;
	align-items: center;
	font-size: 30px;
}

.tabMenuInnerBox {
	display: flex;
	max-width: 1200px;
	width: 100%;
	height: '100%';
	background-color: red;
	align-items: center;
}

.routerBox {
	display: flex;
	max-width: 1200px;
	width: 100%;
	align-items: center;
	margin: 0px auto;
	flex-direction: column;
}

.routerItem {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100px;
	border: 1px black solid;
	width: 100%;
	flex-direction: column;
}